import * as R from 'ramda';

export const sdmx = R.propOr({}, 'sdmx', window.SETTINGS);
export const getDatasource = R.flip(R.prop)(R.prop('datasources', sdmx));
export const sdmxRange = R.prop('range', sdmx);
export const sdmxPeriod = R.path(['period', 'default'], sdmx);
export const sdmxPeriodBoundaries = R.pathOr([], ['period', 'boundaries'], sdmx);
export const sdmxPeriodBoundariesMinMax = {
  min: R.head(sdmxPeriodBoundaries),
  max: R.last(sdmxPeriodBoundaries),
};
export const getSdmxAttribute = id =>
  R.pipe(R.pathOr([], ['attributes', id]), R.ifElse(R.is(Array), R.identity, R.flip(R.append)([])))(
    sdmx,
  );
export const chart = R.propOr({}, 'chart', window.SETTINGS);
export const chartOptions = R.prop('options', chart);

export const getIndicatorProfileUrl = R.path(['unicef', 'indicatorProfileUrl'], window.SETTINGS);
export const getHelpUrl = R.path(['unicef', 'helpUrl'], window.SETTINGS);
export const getHideTotalLabel = R.path(['unicef', 'hideTotalLabel'], window.SETTINGS);
export const showDataflowNavigator = R.path(['unicef', 'showDataflowNavigator'], window.SETTINGS);

export const getMapRefAreaDimId = () => {
  let df = window.DATAFLOW.agencyId + ':' + window.DATAFLOW.dataflowId;
  if (window.SETTINGS.map && window.SETTINGS.map[df])
    return window.SETTINGS.map[df].ref_area_dim_id;
  return null;
};
